import {Observable, Subscription} from 'rxjs'

export class Main {
    private action$: Subscription

    constructor() {
        console.log('wtf')
        this.action$ = Observable.from([10, 20, 30])
            .map(x => x)
            .subscribe(
                x => console.log(x),
                err => console.error(err),
                () => console.log('done')
            )
    }

    start() {
        console.log('heuj')
    }
}

const app = new Main()
app.start()


